.visibility {
  display: none;
}

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.346);
}

.modal-card {
  // display:flex;
  position: absolute;
  width: 882px;
  height: 568px;
  background-color: white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn {
    width: 136px;
    height:44px;
    cursor: pointer;
}
