.cards-container {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.movie-card {
  width: 280px;
  flex-direction: column;
  display: flex;
}

.movie-card__img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 5px;
  display: block;
  overflow: hidden;
}

.movie-card__title {
  flex-grow: 1;
  font-size: 16px;
}

.movie-card__info {
  font-size: 14px;
}

.visibility {
  display: none;
}

.modal-container {
  background-color: rgba(0, 0, 0, .345);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.modal-card {
  width: 882px;
  height: 568px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn {
  width: 136px;
  height: 44px;
  cursor: pointer;
}

